// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getSearchAlbum } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 默认每次请求数量
      limit: 20,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 接收的专辑
      albums: [],
      // 零
      zero: 0,
      // 搜索的关键词
      searchKey: '',
      // 查到的专辑数量
      albumCount: 0,
      // html渲染的歌手
      vHtmlSinger: '',
      // 是否显示v-html嵌入的歌手关键字html标签
      isVHtmlSinger: 0
    };
  },

  mounted() {
    // 获取关键词
    this.searchKey = store.state.searchKey; // 搜索专辑

    if (sessionStorage.getItem('searchKey')) this.getSearchAlbum();
  },

  methods: {
    // 搜索专辑
    async getSearchAlbum() {
      let res = await getSearchAlbum(sessionStorage.getItem('searchKey'), this.limit, this.offset); // console.log('搜索专辑', res);

      this.albumCount = res.data.result.albumCount;
      this.albums = res.data.result.albums;
      if (this.albums.length !== 0) this.isLoading = 0; // console.log(this.albums);
    },

    // 歌手关键词高亮显示
    activeSinger(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlSinger = 0;
        return string;
      } else {
        this.isVHtmlSinger = 1;
        this.vHtmlSinger = string;
        return '';
      }
    },

    // 去往专辑页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 2
        }
      });
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // 重新搜索专辑

      this.getSearchAlbum();
    }

  }
};